import { ForgotpasswrodComponent } from './forgotpasswrod/forgotpasswrod.component';
import { ResetpasswrodComponent } from './resetpasswrod/resetpasswrod.component';
import { RegisterComponent } from './register/register.component';
import { HomeComponent } from './home/home.component';
import { LoginComponent } from './login/login.component';
import { Routes, RouterModule } from '@angular/router';
import { NgModule } from '@angular/core';

const routes: Routes = [
  {path:'login', component: LoginComponent},
  {path:'home', component: HomeComponent},
  {path:'register', component: RegisterComponent},
  {path:'reset-password', component: ResetpasswrodComponent},
  {path:'forgot-password', component: ForgotpasswrodComponent},
]

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule{}
